/* Links */
.main-navigation {
  clear: both;
  display: block;
  height: 54px;
  background: red; }
  .main-navigation ul {
    list-style: none;
    margin: 0;
    padding-left: 0; }
  @media (min-width: 1000px) {
    .main-navigation {
      background: transparent; } }

.nav-hamburger {
  position: absolute;
  width: 54px;
  height: 54px;
  margin: 0;
  transition: .25s;
  z-index: 40;
  padding: 14px 10px; }

.hamburger-container {
  position: relative;
  display: block; }

.nav-hamburger:hover {
  cursor: pointer; }

.hamburger-bar {
  position: absolute;
  width: 100%;
  transition: .5s; }

.nav-hamburger .menu-bar-top {
  border: 4px solid black;
  border-bottom: none;
  top: 0; }

.nav-hamburger .menu-bar-middle {
  height: 4px;
  background-color: black;
  margin-top: 7px;
  margin-bottom: 7px;
  top: 4px; }

.nav-hamburger .menu-bar-bottom {
  border: 4px solid black;
  border-top: none;
  top: 22px; }

.button-open .menu-bar-top {
  -webkit-transform: rotate(45deg) translate(8px, 8px);
  -ms-transform: rotate(45deg) translate(8px, 8px);
  -o-transform: rotate(45deg) translate(8px, 8px);
  transform: rotate(45deg) translate(8px, 8px);
  transition: .5s;
  border: 2px solid black; }

.button-open .menu-bar-middle {
  -webkit-transform: translate(230px);
  -ms-transform: translate(230px);
  -o-transform: translate(230px);
  transform: translate(230px);
  transition: .1s ease-in;
  opacity: 0;
  border: 2px solid white; }

.button-open .menu-bar-bottom {
  -webkit-transform: rotate(-45deg) translate(8px, -7px);
  -ms-transform: rotate(-45deg) translate(8px, -7px);
  -o-transform: rotate(-45deg) translate(8px, -7px);
  transform: rotate(-45deg) translate(8px, -7px);
  transition: .5s;
  border: 2px solid black; }

.menu-main-menu-container {
  background: green;
  top: 0;
  margin-left: -334px;
  width: 334px;
  transition: .25s;
  z-index: 20;
  height: 100%;
  position: fixed; }
  .menu-main-menu-container a {
    text-decoration: none; }
  @media (min-width: 1000px) {
    .menu-main-menu-container {
      background: yellow;
      margin-left: 0;
      width: 300px;
      height: 100%;
      padding: 10px;
      left: 0;
      overflow: hidden;
      text-align: center; } }

.menu-show {
  margin-left: 0; }
  .menu-show .menu {
    margin: 64px 0 0 30px; }

@media (min-width: 1000px) {
  .nav-hamburger {
    display: none; } }

/*--------------------------------------------------------------
# Accessibility
--------------------------------------------------------------*/
/* Text meant only for screen readers. */
.screen-reader-text {
  clip: rect(1px, 1px, 1px, 1px);
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden; }
  .screen-reader-text:focus {
    background-color: #f1f1f1;
    border-radius: 3px;
    box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
    clip: auto !important;
    color: #21759b;
    display: block;
    font-size: 14px;
    font-size: 0.875rem;
    font-weight: bold;
    height: auto;
    left: 5px;
    line-height: normal;
    padding: 15px 23px 14px;
    text-decoration: none;
    top: 5px;
    width: auto;
    z-index: 100000;
    /* Above WP toolbar. */ }

/*--------------------------------------------------------------
# Alignments
--------------------------------------------------------------*/
.alignleft {
  display: inline;
  float: left;
  margin-right: 1.5rem; }

.alignright {
  display: inline;
  float: right;
  margin-left: 1.5rem; }

.aligncenter {
  clear: both;
  display: block;
  margin-left: auto;
  margin-right: auto; }

/*--------------------------------------------------------------
# Clearings
--------------------------------------------------------------*/
.clearfix {
  clear: both;
  content: "";
